import axios from 'axios'
import { Message } from 'element-ui'
import store from '@/store'
import { getToken } from '@/utils/auth'
import qs from 'qs'

let baseURL = ''
if (process.env.NODE_ENV === 'development') {
  // baseURL = 'http://127.0.0.1:8001';
  baseURL = 'http://192.168.50.211:7001';
  
  // baseURL = 'http://192.168.1.25:7001';
  // baseURL = '/api';
}

const service = axios.create({
  baseURL,
  // timeout: 240000  // request timeout455445424
})

// request interceptor
service.interceptors.request.use(config => {
  
  config.headers = {
    'Content-Type': 'application/json;charset=utf-8'
  }
  return config
}, error => {
  // Do something with request error
  Promise.reject(error)
})

const codeMessage = {
  200: '',
  201: '',
  202: '',
  204: '',
  400: '',
  401: '',
  403: '',
  404: '',
  406: '',
  410: '',
  422: '',
  500: '',
  502: '',
  503: '',
  504: ''
}
/**
 * @function 
 * @param  {type} response {description}
 * @return {type} {description}
 */
const checkStatus = response => {
  if (response.status >= 200 && response.status < 300) {
    return response
  }
  const errortext = codeMessage[response.status] || response.statusText
  Message({
    message : ` ${response.status}: ${response.url},${errortext}`,
    type    : 'error',
    duration: 5 * 1000
  })
  const error          = new Error(errortext)
        error.name     = response.status
        error.response = response
  throw error
}
/* Promise(post) */
const fetch = (type, url, params) => {
  return new Promise((resolve, reject) => {
    const data = (type === 'get'|| type === 'delete') ? { params: params } : params
    service[type](url, data)
      .then(checkStatus)
      .then(response => {
        const resultData = response.data;
        let   code       = resultData.code || resultData.R
        if (code === 200 || code === 220 || code === 299) {
          resolve(resultData)
        } else if (code === 1) {
          Message({
            message : resultData.message || resultData.msg,
            type    : 'error',
            duration: 5 * 1000
          })
          reject(new Error(resultData.message))
        } else {
          if (resultData.message !== '') {
            Message({
              message : resultData.message  || resultData.msg,
              type    : 'error',
              duration: 5 * 1000
            })
          }
          reject(new Error(resultData.message))
        }
      }).catch((error) => {
        reject(error)
      })
  })
}

export default {
  fetch,
  baseURL,
}
